<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>商家评论管理管理</span>
      </div>

      <!-- 搜索区域 -->
      <div class="search-box">
        <el-form :inline="true" :model="search">
          <el-form-item>
            <el-input
              maxlength="100"
              v-model="search.keyWorld"
              placeholder="请输入内容"
              style="width: 300px"
            ></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" size="small">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      <!-- 店铺表格区域 -->
      <el-table :data="tableData" style="width: 100%" border stripe>
        <el-table-column label="用户ID" align="center" prop="uname" width="120">
        </el-table-column>
        <el-table-column
          label="菜品名称"
          prop="dname"
          align="center"
          width="150"
        ></el-table-column>
        <el-table-column
          label="评论时间"
          prop="date"
          align="center"
          width="150"
        >
        </el-table-column>
        <el-table-column label="评论详情" prop="detail" align="center">
        </el-table-column>

        <!-- <el-table-column label="操作" align="center">
          <template v-slot="{ row }">
            <el-button type="danger" size="mini" @click="removeFn(row.id)"
              >删除</el-button
            >
          </template>
        </el-table-column> -->
      </el-table>

      <!-- 分页区域 -->
      <el-pagination
        @current-change="handleCurrentChangeFn"
        :current-page.sync="search.pagenum"
        :page-size.sync="search.pagesize"
        layout="total, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import { getComentsListAPI } from "@/api";
export default {
  name: "shop_comments_CMD",
  //   ==============================================以下是data==============================================
  data() {
    return {
      // 评论管理的分页查询参数
      search: {
        pagenum: 1,
        pagesize: 8,
      },

      // 用于控制表单label的宽
      formLabelWidth: "120px",

      // 返回的数据总数
      total: 0,
      // 店铺列表展示数据
      tableData: [],
    };
  },
  //   ==============================================以下是method方法==============================================
  methods: {
    // 初始化文章列表
    async initCommentsListFn() {
      const { data: res } = await getComentsListAPI(this.search);
      if (res.code != 0) return this.$message.error("获取评论列表错误！");
      this.tableData = res.data;
      this.total = res.count;
    },

    // 页码值发生了变化
    handleCurrentChangeFn(newPage) {
      // 为页码值赋值
      this.search.pagenum = newPage;
      // 重新发起请求
      this.initCommentsListFn();
    },
    // 删除
    // async removeFn(id) {
    //   // 1. 询问用户是否要删除
    //   const confirmResult = await this.$confirm(
    //     "此操作将永久删除该文件, 是否继续?",
    //     "提示",
    //     {
    //       confirmButtonText: "确定",
    //       cancelButtonText: "取消",
    //       type: "warning",
    //     }
    //   ).catch((err) => err);

    //   // 2. 取消了删除
    //   if (confirmResult === "cancel") return;

    //   // 执行删除的操作
    //   const { data: res } = await deleteArticleAPI(id);

    //   if (res.code !== 0) return this.$message.error("删除失败!");
    //   this.$message.success("删除成功!");
    //   // 刷新列表数据
    //   this.resetListFn();
    // },
  },
  //   ==============================================以下是mounted方法==============================================
  mounted() {
    this.initCommentsListFn();
  },
};
</script>

<style lang="less" scoped>
.search-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  .btn-pub {
    margin-top: 5px;
  }
}
::v-deep .ql-editor {
  min-height: 300px;
}
// 设置图片封面的宽高
.cover-img {
  width: 400px;
  height: 280px;
  object-fit: cover;
}
// 分页器样式
.el-pagination {
  margin-top: 15px;
  text-align: center;
}
// 美化文章详情的内容样式
.title {
  font-size: 24px;
  text-align: center;
  font-weight: normal;
  color: #000;
  margin: 0 0 10px 0;
}

.info {
  font-size: 12px;
  span {
    margin-right: 20px;
  }
}

// 修改 dialog 内部元素的样式，需要添加样式穿透
::v-deep .detail-box {
  text-align: center;
  img {
    width: 500px;
  }
}
</style>
